import { css, excludeProps, styled } from '@lokalise/styled';

import { withInputErrorModifier } from '../../utils/styles';
import { spacing, typography, value } from '../../utils/theme';

export const Container = styled.div`
	position: relative;
	width: 100%;
	display: grid;
	gap: 4px;
`;

export const IconWrapper = styled.div`
	background-color: ${value('color.background.surface.middle')};
	color: ${value('color.text.input.placeholder')};
	width: 36px;
	border-left: 1px solid ${value('color.border.input.default')};
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	right: 0;
	height: 100%;
	top: 0;
	transition:
		border-color ${value('motion.speed.1')},
		background-color ${value('motion.speed.1')},
		color ${value('motion.speed.1')};
`;

type DateBoxProps = {
	opened: boolean;
	error?: boolean;
};

export const DateBox = styled.button
	.attrs({
		type: 'button',
	})
	.withConfig({ shouldForwardProp: excludeProps(['opened', 'error']) })<DateBoxProps>`
	${typography('body.small.default')};
	border: 1px solid ${value('color.border.input.default')};
	height: ${value('size.height.input.default')};
	width: fit-content;
	color: ${value('color.text.default')};
	padding: ${spacing(1, 3)};
	padding-right: ${spacing(8)};
	border-radius: ${value('radius.input')};
	overflow: hidden;
	background-color: ${value('color.background.input.default')};
	position: relative;
	align-items: center;
	text-align: left;
	transition: border-color ${value('motion.speed.1')};
	cursor: pointer;
	&:focus-visible {
		outline: none; // use hover styles
	}

	// condition to avoid hover overriding style when opened
	${({ opened }) =>
		opened
			? css`
					border-color: ${value('color.border.active')};
					color: ${value('color.text.default')};

					${IconWrapper} {
						border-color: ${value('color.border.active')};
						color: ${value('color.text.default')};
					}
				`
			: css`
					&:hover {
						border-color: ${value('color.border.input.hover')};

						${IconWrapper} {
							border-color: ${value('color.border.input.hover')};
						}
					}
				`}

	${withInputErrorModifier}
`;

export const DrawerContent = styled.div`
	margin: ${spacing(4)};
`;

export const Placeholder = styled.span`
	${typography('body.small.default')}
	color: ${value('color.text.input.placeholder')};
`;

export const DateBoxLabel = styled.span`
	padding-right: ${spacing(2)};
	white-space: nowrap;
`;
